import KButton from 'kolibri.coreVue.components.KButton';

export default {
  name: 'OnboardingForm',
  components: {
    KButton,
  },
  props: {
    header: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: false,
    },
    submitText: {
      type: String,
      required: true,
    },
  },
  computed: {
    hasDescription() {
      return this.description || this.$slots.description;
    },
  },
};

